@import "https://fonts.googleapis.com/css2?family=Arimo:wght@400;700&display=swap";

*, :before, :after {
  box-sizing: border-box;
}

ul[class], ol[class] {
  padding: 0;
}

body, h1, h2, h3, h4, h5, p, ul[class], ol[class], li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

article > * + * {
  margin-top: 1em;
}

input, button, textarea, select {
  font: inherit;
  outline: none;
}

.typography__text-small {
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
}

.typography__text-bold {
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
}

.typography__text-regular {
  font-size: 18px;
  font-weight: 400;
  line-height: 20px;
}

.typography__text-bold--small {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.typography__text-regular--small {
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
}

body {
  color: #fff;
  height: calc(var(--vh, 1vh) * 100);
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: auto-fit auto-fit 1fr auto-fit auto-fit 35px;
  background-color: #888f8f;
  background-image: url("image-1.de20b44b.png");
  background-position: center;
  background-size: cover;
  grid-template-areas: "logo logo logo logo logo logo logo logo logo logo logo logo"
                       "test-top test-top test-top test-top test-top test-top test-top test-top test-top test-top test-top test-top"
                       "main-gap main-gap main-gap main-gap main-gap main-gap main-gap main-gap main-gap main-gap main-gap main-gap"
                       "left-main left-main left-main left-main left-main left-main left-main gap-main right-main right-main right-main right-main"
                       ". . . . . . . . . . . ."
                       ". . . . . . . . . . . ."
                       "footer footer footer footer footer footer footer footer footer footer footer footer";
  grid-auto-flow: row;
  place-content: center;
  gap: 0 20px;
  width: 100%;
  padding: 20px;
  font-family: Arimo, sans-serif;
  display: grid;
  position: relative;
  overflow: auto;
}

@media screen and (width <= 767px) {
  body {
    grid-template: "logo logo logo logo logo logo logo logo logo logo logo logo"
                   "test-top test-top test-top test-top test-top test-top test-top test-top test-top test-top test-top test-top" 100px
                   "main-gap main-gap main-gap main-gap main-gap main-gap main-gap main-gap main-gap main-gap main-gap main-gap" 1fr
                   "left-main left-main left-main left-main left-main left-main left-main left-main left-main left-main left-main left-main"
                   ". . . . . . . . . . . ." 77px
                   "right-main right-main right-main right-main right-main right-main right-main right-main right-main right-main right-main right-main"
                   "footer footer footer footer footer footer footer footer footer footer footer footer" 30px
                   / 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  }
}

body::-webkit-scrollbar {
  width: 0 !important;
}

body video, body audio {
  opacity: 0;
  display: none;
  position: absolute;
  inset: 0;
}

body #transition-container {
  position: absolute;
  inset: 0;
}

body #transition-container #slider {
  width: 100%;
  height: 100%;
  transition: all 2s ease-in;
}

body #transition-container .blur {
  background-color: #00000070;
  position: absolute;
  inset: 0;
}

.section-top {
  grid-area: test-top;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas: "left-top left-top left-top left-top left-top left-top left-top gap-top right-top right-top right-top right-top";
  gap: 0 20px;
  height: -moz-fit-content;
  height: fit-content;
  display: grid;
  overflow: hidden;
}

button.sound {
  color: #ffffff83;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  font-size: 18px;
  font-weight: 400;
  line-height: 20px;
  display: flex;
}

button.sound:hover {
  color: #fff;
}

@media screen and (width <= 767px) {
  button.sound {
    flex-direction: row-reverse;
    gap: 10px;
    margin-left: 0;
    padding-left: 0;
  }
}

button.sound.pause .line-1, button.sound.pause .line-2, button.sound.pause .line-3 {
  background-color: #fff;
}

section.logo {
  grid-area: logo;
  margin-bottom: 50px;
  position: relative;
}

section.logo #canvas-container {
  transition: all 2s ease-in;
  position: absolute;
  inset: 0;
}

section.logo img.logo {
  min-width: 100%;
  animation: 2s forwards step-one;
}

section.logo img.logo.step-two {
  clip-path: polygon(0 0, 30% 0, 30% 100%, 0 100%);
  animation: 2s forwards step-two;
}

section.logo img.logo.step-done {
  clip-path: polygon(0 0, 80% 0, 80% 100%, 0 100%);
  animation: .5s forwards step-done;
}

section.logo img.logo.hidden {
  opacity: 0;
}

section.logo img.alpha-glass, section.logo img.displacement {
  display: none;
}

section.logo .logo-wrapper {
  width: 100%;
  overflow: hidden;
}

section.logo span.loading {
  color: #ffffff83;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  position: absolute;
  bottom: 0;
  right: 0;
}

@media screen and (width <= 767px) {
  section.logo span.loading {
    display: none;
  }
}

section.logo span.loading span:first-child {
  animation: .3s ease-in-out infinite animate-dots;
  position: relative;
}

section.logo span.loading span:nth-child(2) {
  animation: .6s ease-in-out infinite animate-dots;
  position: relative;
}

section.logo span.loading span:nth-child(3) {
  animation: .9s ease-in-out infinite animate-dots;
  position: relative;
}

section.logo span.loading.done {
  opacity: 0;
}

@media screen and (width <= 767px) {
  section.logo:not(img) {
    margin-bottom: 30px;
  }
}

